<template>
  <div>
    <div class="cart" v-show="!isPay">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>购物车</span>
        </div>
        <div class="cart-main">
          <div class="cart-th">
            <div class="cart-th1">全部</div>
            <div class="cart-th2">商品</div>
            <div class="cart-th3">单价（元）</div>
            <div class="cart-th4">数量</div>
            <div class="cart-th5">小计（元）</div>
            <div class="cart-th6">操作</div>
          </div>
          <div class="cart-body">
            <ul class="cart-list" v-for="cart in cartInfoList" :key="cart.id">
              <!-- 是否选中 -->
              <li class="cart-list-con1">
                <input
                  type="checkbox"
                  name="chk_list"
                  :checked="cart.status == 0"
                  @change="updateChecked(cart, $event)"
                />
              </li>
              <!-- 菜品图片和菜品名 -->
              <li class="cart-list-con2">
                <img :src="cart.dimage" />
                <div class="msg">
                  <div class="shopname">{{ cart.sname }}</div>
                  <div class="item-msg">
                    {{ cart.dname }}
                  </div>
                </div>
                <el-button type="success" disabled v-if="cart.dstatus == 0">
                  在售
                </el-button>
                <el-button type="info" disabled v-else> 待售 </el-button>
              </li>
              <!-- 菜品单价 -->
              <li class="cart-list-con4">
                <span class="price">{{ cart.dprice }}</span>
              </li>
              <!-- 修改数量 -->
              <li class="cart-list-con5">
                <a
                  href="javascript:void(0)"
                  class="mins"
                  @click="handler('mins', -1, cart)"
                >
                  -
                </a>
                <input
                  autocomplete="off"
                  type="text"
                  :value="cart.count"
                  minnum="1"
                  class="itxt"
                  @change="handler('change', $event.target.value * 1, cart)"
                />
                <a
                  href="javascript:void(0)"
                  class="plus"
                  @click="handler('add', 1, cart)"
                  >+</a
                >
              </li>
              <!-- 计算总价 -->
              <li class="cart-list-con6">
                <span class="sum">{{ cart.count * cart.dprice }}</span>
              </li>
              <!-- 操作 -->
              <li class="cart-list-con7">
                <el-button
                  type="danger"
                  class="sindelet"
                  @click="deleteButtonONE(cart)"
                  >删除</el-button
                >
              </li>
            </ul>
          </div>
        </div>
        <div class="cart-tool">
          <!-- 全选 -->
          <div class="select-all">
            <input
              class="chooseAll"
              type="checkbox"
              :checked="isAllChecked && cartInfoList.length > 0"
              @change="updateAllCartChecked"
              :disabled="cartInfoList.length == 0"
            />
            <span>全选</span>
          </div>
          <!-- 其他操作 -->
          <div class="option">
            <el-button type="danger" size="mini" @click="deleteButtonTWO">
              删除选中的菜品
            </el-button>
            <el-button type="danger" size="mini" @click="deleteAllForsaleCart"
              >清除下架菜品</el-button
            >
          </div>
          <!-- 结算 -->
          <div class="money-box">
            <div class="chosed">
              已选择 <span>{{ chosedNum }}</span
              >件菜品
            </div>
            <div class="sumprice">
              <em>总价 ：</em>
              <i class="summoney">{{ totalPrice }}</i>
            </div>
            <div class="sumbtn">
              <a class="sum-btn" style="cursor: pointer" @click="calculate"
                >结算</a
              >
            </div>
          </div>
        </div>
      </el-card>
    </div>
    <div class="two" v-show="isPay">
      <el-result title="请使用支付宝支付">
        <template slot="icon">
          <img class="payIcon" src="@/assets/images/ZFB.png" />
        </template>
        <template slot="extra">
          <el-button type="primary" size="medium" @click="payNow"
            >确认支付</el-button
          >
        </template>
      </el-result>
    </div>
  </div>
</template>

<script>
import throttle from "lodash/throttle";
import { mapGetters } from "vuex";
import { createOrderByShopCartAPI } from "@/api";

export default {
  name: "shopcart",
  //   ==============================================以下是data==============================================
  data() {
    return {
      // 用于切换支付页面
      isPay: false,
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    // 获取数据库里购物车的列表
    getData() {
      this.$store.dispatch("getCartList");
    },
    // 修改购物车菜品数量
    handler: throttle(async function (type, disNum, cart) {
      switch (type) {
        case "add":
          disNum = 1;
          break;
        case "mins":
          disNum = cart.count > 1 ? -1 : 0;
          break;
        case "change":
          if (isNaN(disNum) || disNum < 0) {
            disNum = 0;
          } else {
            if (parseInt(disNum) > 20) {
              this.$message.error("您已超过最大限购数量！");
              return this.getData();
            }
            disNum = parseInt(disNum) - cart.skuNum;
          }
          break;
      }
      try {
        await this.$store.dispatch("updateShopCart", {
          cartID: cart.carid,
          num: disNum,
        });
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    }, 10),
    // 删除按钮
    async deleteButtonONE(cart) {
      const confirmResult = await this.$confirm(
        "此操作将永久删除该记录, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);

      // 2. 取消了删除
      if (confirmResult === "cancel") return;
      this.deleteCartById(cart);
    },
    // 删除菜品
    async deleteCartById(cart) {
      try {
        await this.$store.dispatch("deleteCartListBySkuId", cart.carid);
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
    // 修改选中状态
    async updateChecked(cart, event) {
      try {
        let isChecked = event.target.checked ? "0" : "1";
        await this.$store.dispatch("updateCheckedById", {
          cartID: cart.carid,
          isChecked: isChecked,
        });
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
    async deleteButtonTWO() {
      const confirmResult = await this.$confirm(
        "此操作将永久删除勾选记录, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).catch((err) => err);
      // 2. 取消了删除
      if (confirmResult === "cancel") return;
      this.deleteAllCheckedCart();
    },
    // 删除选中的所有菜品
    async deleteAllCheckedCart() {
      try {
        await this.$store.dispatch("deleteAllCheckedCart");
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
    // 删除所有下架菜品
    async deleteAllForsaleCart() {
      try {
        await this.$store.dispatch("deleteAllForsaleCart");
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
    // 修改全选
    async updateAllCartChecked(event) {
      try {
        let isChecked = event.target.checked ? "0" : "1";
        await this.$store.dispatch("updateAllCartChecked", isChecked);
        this.getData();
      } catch (error) {
        alert(error.message);
      }
    },
    calculate() {
      this.isPay = true;
    },
    payNow() {
      this.calculateFn();
      this.isPay = false;
    },
    async calculateFn() {
      const { data: res } = await createOrderByShopCartAPI();
      if (res.code != 0) return this.$message.error("支付失败！");
      this.$message.success("购买成功！");
      this.getData();
    },
  },
  //   ==============================================以下是created方法==============================================
  mounted() {
    this.getData();
  },
  //   ==============================================以下是Computed方法==============================================
  // 计算属性
  computed: {
    ...mapGetters(["cartList"]),
    cartInfoList() {
      return this.cartList || [];
    },
    // 计算产品的总价
    totalPrice() {
      let sum = 0;
      this.cartInfoList.forEach((item) => {
        if (item.status == 0) {
          sum += item.dprice * item.count;
        }
      });
      return sum;
    },
    // 是否全选
    isAllChecked() {
      return this.cartInfoList.every((item) => item.status == 0);
    },
    // 已选中菜品数量
    chosedNum() {
      let num = 0;
      this.cartInfoList.forEach((item) => {
        if (item.status == 0) {
          num++;
        }
      });
      return num;
    },
  },
};
</script>

<style lang="less" scoped>
li {
  list-style: none;
}
a {
  text-decoration: none;
}

.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 15%;
        }

        .cart-list-con2 {
          width: 35%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .shopname {
            float: left;
            width: 150px;
            margin: 0 10px;
            color: #615c5c;
            font-family: "楷体";
            line-height: 18px;
          }
          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 20px;
            font-size: 15px;
          }
        }

        .cart-list-con4 {
          width: 10%;
        }

        .cart-list-con5 {
          width: 17%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 10%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 13%;

          a {
            color: #666;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
.payIcon {
  width: 300px;
}
</style>
